export function getData() {
  return blog_data;
}

export function getDataID(id) {
  return blog_data.find((data) => data.id === id);
}

export function getDataURL(url) {
  return blog_data.find((data) => data.url === url);
}
export function getTitleID(title) {
    let info =  blog_data.find(
        (data) => data.title === title
    );
    let title_id = info.id;
return title_id;
}

export function deleteData(number) {
  deleteData = blog_data.filter((data) => data.number !== number);
}

